<template>
  <div class="enterpriserecommend padd">
      <h1>企业推荐</h1>
      <div class="banner">
          <img src="../../assets/test/suannai1.png" alt="">
          <p class="tc two_t_h">得乐蛋白五盒装</p>
      </div>
      <div class="gooodslist">
          <ul class="clearfix">
              <li class="item fl" v-for="i in 10" :key="i">
                <a href="#">
                  <img src="../../assets/test/goods.png" alt="">
                  <p class="">
                    <span class=" price red_love">¥57.00 (9.5 折)</span>
                    <span class=" iconfont icon-aixin grey_love ico_show ico_show" @click.stop="collect()" :class="{red_love : isCollect}"></span>
                    <span class=" iconfont icon-aixin grey_love ico_show ico_hide" @click.stop="collect()" :class="{red_love : isCollect, fadeOutUp:isCollect}"></span>
                  </p>
                  <span class="gouwudai iconfont icon-gouwudai"></span>
                </a>
                <p class="describe two_t_h">芒果酸奶</p>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
  name: "Enterpriserecommend",
  data() {
    return {
        isCollect : false,
    };
  },
  methods: {
    collect() {
      this.isCollect = !this.isCollect;
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.enterpriserecommend {
  font-size: 0.16rem;
  .banner {
    img {
      display: block;
      width: 3.2rem;
      height: 1.7rem;
      border-radius: 0.12rem;
      margin-top: 0.15rem;
    }
    p {
      padding: 0.05rem 0 0.3rem 0;
    }
  }
  .gooodslist {
    .item {
      margin-bottom: 0.2rem;
      &:nth-child(even) {
        margin-left: 0.2rem;
      }
      a {
        width: 1.5rem;
        height: 1.5rem;
        display: block;
        position: relative;
        border-radius: 0.12rem;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          display: block;
          background-color: hotpink;
        }
        p {
          font-size: 0.16rem;
          position: absolute;
          bottom: 0rem;
          left: 0rem;
          padding: 0rem 0.15rem 0.1rem 0.1rem;
          width: 100%;
          .price {
            font-family: PingFangSC-Regular;
            font-size: 0.12rem;
            color: #e7290f;
            letter-spacing: 0;
          }
          .ico_show {
            position: absolute;
            top: 0rem;
            right: 0.15rem;
          }
          .ico_hide {
            position: absolute;
            top: 0rem;
            right: 0.15rem;
          }
        }
        .gouwudai {
            position: absolute;
            top: 0.1rem;
            right: 0.17rem;
            font-size: 0.16rem;
        }
      }
      .describe {
        margin-top: 0.05rem;
      }
    }
  }
}
</style>



